import { Component } from '@angular/core';

import { Th2NovaSpinnerService } from '../../../utils/components/th2-nova-spinner-component/th2-nova-spinner.service';

@Component({
    selector: 'logo-icon-view-component',
    templateUrl: 'logo-icon-view.component.html'
})
export class LogoIconViewComponent {
    constructor(private spinnerService: Th2NovaSpinnerService) {}

    public showSpinner() {
        this.spinnerService.display(true);
        setTimeout(() => {
            this.spinnerService.display(false);
        }, 3000);
    }
}
