import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { LogoIconViewComponent } from './logo-icon-view.component';

const routes: Routes = [{ path: '', component: LogoIconViewComponent }];

@NgModule({
    exports: [RouterModule],
    imports: [RouterModule.forChild(routes)]
})
export class LogoIconViewRoutingModule {}
