import { Injectable } from '@angular/core';

import { BehaviorSubject, Observable } from 'rxjs';

@Injectable({
    providedIn: 'root'
})
export class Th2NovaSpinnerService {
    /**
     * <p><code>BehaviorSubject</code> which save and update the spinner state.</p>
     */
    public status: BehaviorSubject<boolean> = new BehaviorSubject<boolean>(false);

    /**
     * <p><code>Number</code> which count the number of times it is called to activate
     * the spinner.</p>
     */
    private cont = 0;

    /**
     * <p>Method that handles she spinner visibility. </p>
     *
     * @param value boolean specify the new spinner state.
     */
    public display(value: boolean) {
        if (value) {
            this.cont++;
            if (!this.status.getValue()) {
                this.status.next(value);
            }
        } else {
            if (this.cont === 0) {
                this.status.next(value);
            } else {
                this.cont--;
                if (this.cont === 0) {
                    this.status.next(value);
                }
            }
        }
    }

    /**
     * <p>Method that return the status subject. </p>
     */
    public getStatus(): Observable<boolean> {
        return this.status.asObservable();
    }
}
